import React from 'react';
import {
  BrowserRouter as 
  Router,
  Route,
  Routes,
  Link,
} from "react-router-dom";
import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';

import './App.css';
import './index.css';

import Footer from './components/Footer';
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
//import ContactPage from './pages/ContactPage';

class App extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      title: 'RK Technologies',
      headerLinks: [
        {title: 'Home', path: '/'},
        {title: 'About', path: '/about'},
        // {title: 'Contact', path: '/contact'}
      ],
      home: {
        title: 'Web Developer By Day, Game Developer By Night',
        subTitle: 'Rami Kanawati',
        text: 'Aspiring Software Developer'
      },
      about: {
        title: 'About Me',
      }
      // },
      // contact: {
      //   title: 'Contact Me',
      // }
    }
  }
  
  render(){

    return (
      
      <Router>
        <Container fluid={true} className="myMainContainer">
          <Navbar className="border-bottom" bg="transparent" expand="lg">
            <Navbar.Brand>RK Technologies</Navbar.Brand>
            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle"/>
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ms-auto">
                <Link className="nav-link" to="/">Home</Link>
                <Link className="nav-link" to="/about">About Me</Link>
                {/* <Link className="nav-link" to="/contact">Contact Me</Link> */}
              </Nav>
            </Navbar.Collapse>
          </Navbar>
          <Routes>
            <Route path="/" element={<HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />} />
            <Route path="/about" element={<AboutPage title={this.state.about.title}/>} />
            {/* <Route path="/contact" element={<ContactPage title={this.state.contact.title}/>} /> */}
          </Routes>
          <Footer/>
        </Container>
      </Router>    
      
      );
  }
}

export default App;