import React from 'react';

import gif01 from '../assets/images/gif01.gif';
import gif02 from '../assets/images/gif02.gif';
import gif03 from '../assets/images/gif03.gif';

import Card from '../components/Card';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import '../index.css';

class Carousel extends React.Component {

    constructor(props) {
        super(props);
        this.state = 
        {
            items: 
            [
                {
                    id: 0,
                    title: 'Home Invasion',
                    subTitle: 'An Unreal Engine Game',
                    imgSrc: gif01,
                    link: 'https://youtu.be/a_8ZrLQcPZo',
                    selected: false
                },

                {
                    id: 1,
                    title: 'Dungeon Crawler',
                    subTitle: 'A Unity 3D Game',
                    imgSrc: gif02,
                    link: 'https://youtu.be/g8e0kpz2-Rw',
                    selected: false
                },

                {
                    id: 2,
                    title: 'Mountain Run',
                    subTitle: 'A 3D Modelling and Animation Project',
                    imgSrc: gif03,
                    link: 'https://youtu.be/2znYKqeg9u8',
                    selected: false
                },
            ]
        }
    }

    handleCardClick = (id, card) => {
        let items = [...this.state.items];

        items[id].selected = items[id].selected ? false : true;

        items.forEach(item => {

            if(item.id !== id){
                item.selected = false;
            } 
            }
        )
        
        this.setState({
            items
        });
    }

    makeItems = (items) => {
        return items.map(item => {
            return <Card item={item} click={(e => this.handleCardClick(item.id, e))} key={item.id}/>
        })
    }

    render() { 
        return(
            <Container fluid={true}>
                <Row >
                    <Col className="myCarousel">{this.makeItems(this.state.items)}</Col>
                </Row>
            </Container>
        );
    }
}

export default Carousel;