import React from 'react';

import CardInfo from '../components/CardInfo';
import '../index.css';

function Card(props) {
    return(
        <div className="d-inline-block myCards" onClick={(e)=>props.click(props.item)}>
            <img className="myCardsImage" src={props.item.imgSrc} alt={props.item.imgSrc} />
            { props.item.selected && <CardInfo title={props.item.title} subTitle={props.item.subTitle} link={props.item.link} /> }
        </div>
    );
}

export default Card;
