import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import '../index.css';

function Hero(props){
    return(
        <div className="Jumbotron bg-transparent jumbotron-fluid p-0">
            <Container fluid={true}>
                <Row className="justify-content-center py-5">
                    <Col md={8} sm={12}>
                        { props.title && <h1 className="myHeroTitle">{props.title}</h1>}
                        { props.subTitle && <h3 className="display-4">{props.subTitle}</h3>}
                        { props.text && <h3 className="lead">{props.text}</h3>}
                    </Col>
                </Row>
            </Container>
        </div>
    );
}

export default Hero;