import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebook, faInstagram, faGithub, faLinkedin, faYoutube } from '@fortawesome/free-brands-svg-icons';

function Footer(){

    return(
        <footer className="mt-5">
            <Container fluid={true} className="myFooter">
                <Row className="border-top justify-content-between p-3">
                    <Col className="p-0" md={3} sm={12}>
                      <a href="https://www.github.com/ramikanawati" target="_blank" rel="noopener norefferer"><FontAwesomeIcon className="logos w3-hover-opacity" icon={faGithub} /></a>
                      <a href="https://www.facebook.com/rkanawati96" target="_blank" rel="noopener norefferer"><FontAwesomeIcon className="logos w3-hover-opacity" icon={faFacebook} /></a> 
                      <a href="https://www.instagram.com/rami_kanawati/" target="_blank" rel="noopener norefferer"><FontAwesomeIcon className="logos w3-hover-opacity" icon={faInstagram} /></a> 
                      <a href="https://www.youtube.com/channel/UCSKbvK9feFJpXkNOyoxODDQ" target="_blank" rel="noopener norefferer"><FontAwesomeIcon className="logos w3-hover-opacity" icon={faYoutube} /></a> 
                      <a href="https://www.linkedin.com/in/rkanawati96" target="_blank" rel="noopener norefferer"><FontAwesomeIcon className="logos w3-hover-opacity" icon={faLinkedin} /></a> 
                    </Col>
                    <Col className="p-0 d-flex justify-content-end" md={3}>
                        Developed By Rami Kanawati
                    </Col>
                </Row>
            </Container>
        </footer>
    );
}

export default Footer;