import React from 'react';

import Hero from '../components/Hero';
import Content from '../components/Content';

//import Row from 'react-bootstrap/Row';
//import Col from 'react-bootstrap/Col';
//import profilePic from '../assets/images/profilePic.jpeg';

import '../index.css';

function AboutPage(props){

    return(
        <div style={{"paddingBottom":"30px"}}>
            <Hero title={props.title} />
            <Content>
            <p className="lead">
                I am a Computer Science Student at the University of Wollongong in Dubai, My Game Development experience is within Unity 3D, 
                Unreal Engine, and some experiece with Autodesk Maya for 3D Modelling and Animation, in order to be able to create my Assets. 
                On the other hand, My Web Development experience is within HTML, CSS, Javascript, React.Js, Node.Js and PHP. I am currently 
                also pursuing a career in Mobile Development using React Native, Firebase, AWS Amplify and many other AWS services
                as well as, MongoDB for the backend.
            </p>
            <br></br>
            <h3>My Skills</h3><br></br>
            <p className="w3-wide">Game Development</p>
            <div className="w3-white">
                <div className="w3-dark-grey" style={{"height":"28px", "width":"80%"}}></div>
            </div><br></br>
            <p className="w3-wide">Web Development</p>
            <div className="w3-white">
                <div className="w3-dark-grey" style={{"height":"28px", "width":"75%"}} ></div>
            </div><br></br>
            <p className="w3-wide">Mobile Development</p>
            <div className="w3-white">
                <div className="w3-dark-grey" style={{"height":"28px", "width":"60%"}}></div>
            </div>
            </Content>
        </div>
    );
}

export default AboutPage;